<template>
  <v-card dark style="margin: 5px">
    <v-card-title>
      <v-list-item three-line>
      <v-list-item-content>
        <div class="text-overline mb-4">
           <v-app-bar
            flat
            color="rgba(0, 0, 0, 0)"
          >
            <v-toolbar-title >
              
            </v-toolbar-title>
            <v-spacer></v-spacer>

            <v-btn
              color="white"
              icon @click = goToAlignment
            >
              <v-icon>mdi-arrow-up-bold-box-outline</v-icon>
            </v-btn>
          </v-app-bar>
        </div>
        <v-list-item-title class="text-h5 mb-1">
          DeepLoc results
        </v-list-item-title>
      </v-list-item-content>

      <v-list-item-avatar
        tile
        size="50"
        color="#BA55D3"
      ></v-list-item-avatar>
    </v-list-item>
          </v-card-title>
    <v-card-text>
      DeepLoc 2.0 uses protein language models to predict multi-label subcellular localization. This software uses a pre-trained protein language model to find signal peptides to specify the localization on cellular level.
      <a href="https://academic.oup.com/nar/article/50/W1/W228/6576357" target="_blank">Go to DeepLoc 2.0 <v-icon>mdi-open-in-new</v-icon></a>
      <div>
        <v-data-table
          dense
          dark
          style="margin: 5px"
          :headers="headers"
          :items="dlinfo"
          :items-per-page="-1"
          class="elevation-1"
        >
        <template v-slot:item="i">
          <tr>
            <td>{{i.item.localization}}</td>
            <td>{{i.item.signals}}</td>
            <td :class="{'purple lighten-3': i.item.cytoplasm > 0.5}">{{i.item.cytoplasm}}</td>
            <td :class="{'purple lighten-3': i.item.nucleus > 0.5}">{{i.item.nucleus}}</td>
            <td :class="{'purple lighten-3': i.item.extracellular > 0.5}">{{i.item.extracellular}}</td>
            <td :class="{'purple lighten-3': i.item.cell_membrane > 0.5}">{{i.item.cell_membrane}}</td>
            <td :class="{'purple lighten-3': i.item.peroxisome > 0.5}">{{i.item.peroxisome}}</td>
            <td :class="{'purple lighten-3': i.item.mitochondrion > 0.5}">{{i.item.mitochondrion}}</td>
            <td :class="{'purple lighten-3': i.item.plastid > 0.5}">{{i.item.plastid}}</td>
            <td :class="{'purple lighten-3': i.item.endoplasmic_reticulum > 0.5}">{{i.item.endoplasmic_reticulum}}</td>
            <td :class="{'purple lighten-3': i.item.lysosome_or_vacuole > 0.5}">{{i.item.lysosome_or_vacuole}}</td>
            <td :class="{'purple lighten-3': i.item.golgi_apparatus > 0.5}">{{i.item.golgi_apparatus}}</td>
          </tr>
        </template>

        </v-data-table> 
        </div>
        <v-container fluid>
  <v-layout justify-center align-center>
    <v-flex shrink>
      <div>
        <v-img
        v-if="proteinId"
        :src="image"
        contain
        class="mx-auto"
        width="60%"
      />
      </div>
    </v-flex>
  </v-layout>
</v-container>
        
         
       

     
    </v-card-text>
  </v-card>
</template>
    
<script>
export default {
  data() {
    return {
      image: "",
      headers: [],
      dlinfo: [],
    };
  },
  props: {
    proteinId: {
      required: true,
      type: String,
    },
  },
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    var deeplog_png = entry["deeploc_png"];
    if (deeplog_png.startsWith('b\'')) {
      deeplog_png = deeplog_png.substring(2, deeplog_png.length - 1);
    }
    //const base64Str = Buffer.from(deeplog_png).toString('base64');
    this.image = 'data:image/png;base64,' + deeplog_png;

    const deeploc_info = entry["deeploc_info"][0];
    this.dlinfo.push({
      localization: deeploc_info[0]['localization'],
      signals: deeploc_info[0]['signals'],
      cytoplasm: (parseFloat(deeploc_info[0]['cytoplasm'])).toFixed(4), 
      nucleus: (parseFloat(deeploc_info[0]['nucleus'])).toFixed(4), 
      extracellular: (parseFloat(deeploc_info[0]['extracellular'])).toFixed(4),
      cell_membrane: (parseFloat(deeploc_info[0]['cell_membrane'])).toFixed(4),
      peroxisome: (parseFloat(deeploc_info[0]['peroxisome'])).toFixed(4),
      mitochondrion: (parseFloat(deeploc_info[0]['mitochondrion'])).toFixed(4),
      plastid: (parseFloat(deeploc_info[0]['plastid'])).toFixed(4), 
      endoplasmic_reticulum: (parseFloat(deeploc_info[0]['endoplasmic_reticulum'])).toFixed(4),
      lysosome_or_vacuole: (parseFloat(deeploc_info[0]['lysosome_or_vacuole'])).toFixed(4),
      golgi_apparatus: (parseFloat(deeploc_info[0]['golgi_apparatus'])).toFixed(4), 

    }); 
//localization, signals,cytoplasm,nucleus,  extracellular,cell_membrane, mitochondrion,plastid,  endoplasmic_reticulum, lysosome_or_vacuole, golgi_apparatus, peroxisome}])
    this.headers = [
      {
        text: "Localization",
        align: "start",
        sortable: false,
        value: "localization",
      },
      { text: "Signals", value: "signals" },
      { text: "Cytoplasm", value: "cytoplasm" },
      { text: "Nucleus", value: "nucleus" },
      { text: "Extracellular", value: "extracellular" },
      { text: "Cell membrane", value: "cell_membrane" },
      { text: "Peroxisome", value: "peroxisome" },
      { text: "Mitochondrion", value: "mitochondrion" },
      { text: "Plastid", value: "plastid" },
      { text: "Endoplasmic reticulum", value: "endoplasmic_reticulum" },
      { text: "Lysosome/Vacuole", value: "lysosome_or_vacuole" },
      { text: "Golgi apparatus", value: "golgi_apparatus" },
    ];
  },
};
</script>
    
<style lang="css">
</style> 

