<template>
  <v-card dark>
    <v-card-title>
      <div>Offtargets</div>
    </v-card-title>
    <v-card-text>
      Offtargets show if the found peptide occurs in other proteins as well.
      <div>
        <v-data-table
          dense
          dark
          style="margin: 5px"
          :headers="headers"
          :items="info"
          :items-per-page="-1"
          class="elevation-1"
        >
        </v-data-table>
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data() {
    return {
      headers: [],
      info: [],
    };
  },
  components: {},
  mounted() {
    // let project_information = this.$store.state.data;
    // for (const projectID in project_information) {
    //     const project = project_information[projectID];
    //     const fragment_information = project.fragment_information;
    //     for (const fragment of fragment_information) {
    //const genename = fragment.genename;
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    let offtargets = entry.offtargets;
    let found_offtargets = [];
    if (offtargets != "None") {
        offtargets = offtargets.split("}},");
        for (let target of offtargets) {
            //counter += 1;
            target = target.replaceAll("{", "").replaceAll("}", "");
            const proteinID = (target.split(":")[0]).replaceAll(" ", "");
            
            const off_gene_name = target.split("gene_name: ")[1].split(",")[0];
            const header = target.split("header: ")[1].split(",")[0];
            const db_xref = target.split("db_xref: ")[1].split(",")[0];
            const geneID = "GeneID:" + target.split("GeneID:")[1].split(",")[0];
            const off_protein_name = target
            .split("protein_name: ")[1]
            .split(",")[0];
            const transl_except = target.split("transl_except: ")[1].split(",")[0];
            const frame = target.split("frame: ")[1].split(",")[0];
            const partial = target.split("partial: ")[1].split(",")[0];
            const exception = target.split("exception: ")[1].split(",")[0];
            //NP_001265045.1: gene_name: L1CAM, isoform_info: protein_ID: NP_001265045.1, header: lcl|NC_000023.11_cds_NP_001265045.1_129697 ,
            //db_xref: CCDS:CCDS14733.1,Ensembl:ENSP00000359077.1,GeneID:3897, protein_name: neural cell adhesion molecule L1 isoform 1 precursor,
            //transl_except: None, frame: , partial: None, exception: None
            if(!(found_offtargets.includes(proteinID))){
                found_offtargets.push(proteinID);
                //console.log(found_offtargets);
                this.info.push({
                proteinID: proteinID,
                gene_name: off_gene_name,
                header: header,
                db_xref: db_xref,
                geneID: geneID,
                protein_name: off_protein_name,
                transl_except: transl_except,
                frame: frame,
                partial: partial,
                exception: exception,
                });
            }
        }
    }

    this.headers = [
      {
        text: "ProteinID",
        align: "start",
        sortable: false,
        value: "proteinID",
      },
      { text: "Gene name", value: "off_gene_name" },
      { text: "Header", value: "header" },
      { text: "db_xref", value: "db_xref" },
      { text: "GeneID", value: "geneID" },
      { text: "Protein name", value: "off_protein_name" },
      { text: "Translation exception", value: "transl_except" },
      { text: "Frame", value: "frame" },
      { text: "Partial", value: "partial" },
      { text: "Exception", value: "exception" },
    ];
  },
};
</script>
