<template>
  <v-card dark>
    <v-card-title>
      <div>MaxQuant Results</div>
    </v-card-title>
    <v-card-text>
      <div>
        <v-data-table
          dense
          dark
          style="margin: 5px"
          :headers="headers"
          :items="mqinfo"
          :items-per-page="-1"
          class="elevation-1"
        >
        </v-data-table>
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data() {
    return {
      headers: [],
      mqinfo: [],
    };
  },
  components: {},
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    this.fragment = entry["peptide"];
    let hits = entry["protein_name"].split(";");
    //console.log(entry["protein_name"]);
    //NP_066944.1_S|elongation factor 1-beta [Homo sapiens];
    //NP_001950.1_S|elongation factor 1-beta [Homo sapiens];
    //NP_001032752.1_S|elongation factor 1-beta [Homo sapiens];
    //NP_066944.1_A|elongation factor 1-beta [Homo sapiens];
    //NP_001950.1_A|elongation factor 1-bet
    let i = 1;
    for (const hit of hits) {
      const proteinID = hit.split("|")[0];
      let proteinName = "";
      if (hit.split("|")[1]) {
        proteinName = hit.split("|")[1];
        proteinName = proteinName.split("[")[0];
      }
      let organismName = "";
      if(hit.split("[")[1]){
        organismName = (hit.split("[")[1]).replace("]", "");
      }
      
      this.mqinfo.push({
        rank: i,
        ID: proteinID,
        name: proteinName,
        organism: organismName,
      });

      i += 1;
    }

    this.headers = [
      {
        text: "Rank",
        align: "start",
        sortable: false,
        value: "rank",
      },
      { text: "ID", value: "ID" },
      { text: "name", value: "name" },
      { text: "Organism", value: "organism" },
    ];
  },
};
</script>
