<template>
  <div>
    <v-card dark>
      <v-card-title>
        <!-- <div>BlastP Results</div> -->
         <v-list-item three-line>
      <v-list-item-content>
        <div class="text-overline mb-4">
           <v-app-bar
            flat
            color="rgba(0, 0, 0, 0)"
          >
            <v-toolbar-title >
              
            </v-toolbar-title>
            <v-spacer></v-spacer>

            <v-btn
              color="white"
              icon @click = goToAlignment
            >
              <v-icon>mdi-arrow-up-bold-box-outline</v-icon>
            </v-btn>
          </v-app-bar>
        </div>
        <v-list-item-title class="text-h5 mb-1">
          BLASTp results
        </v-list-item-title>
      </v-list-item-content>

      <v-list-item-avatar
        tile
        size="50"
        color="#6495ED"
      ></v-list-item-avatar>
    </v-list-item>
       
      </v-card-title>
      <v-card-text>
          <v-data-table
            dense
            dark
            style="margin: 5px"
            :headers="headers"
            :items="blast_info"
            :items-per-page="10"
            :sort-by="['evalue']"
            :sort-desc="[false]"
            class="elevation-1"
          >
          </v-data-table>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>

export default {
  data() {
    return {
      headers: [],
      blast_info: [],
    };
  },
  props: {
    proteinId: {
      required: true,
      type: String,
    },
  },
  components: {},
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    let blast_info_json = entry["blast_info"];

    let found_blast_hits = [];
    let blast_info = [];
    for (let result_list of blast_info_json) {
      for(let result of result_list){
        if (result.length != 0) {
            let hitID = result[0];
            let database = result[1];
            let name = result[2];
            let evalue = result[5];
            let organism1 = result[3].split(" ");
            let organism = organism1[0] + " "+ organism1[1];
            if(!(found_blast_hits.includes((hitID,name)))){
              found_blast_hits.push((hitID,name));
              blast_info.push({
                  database: database,
                  hitID: hitID,
                  name: name,
                  evalue: evalue,
                  organism: organism,
                  })
            }
        }
      }
    }
    this.blast_info = blast_info;

    this.headers = [
      {
        text: "E value",
        align: "start",
        sortable: false,
        value: "evalue",
      },
      { text: "Hit ID", value: "hitID"},
      { text: "Name", value: "name"},
      { text: "Database", value: "database" },
      { text: "Organism", value: "organism" },
    ];
  },
};
</script>