<template>
  <div>
    <v-col cols="12" sm="12" md="12">
      <v-row>
        <v-card dark style="margin: 10px; width: 100%">
          <v-card-title>
            
          </v-card-title>
          <v-card-text>
            Click paper clip to upload your data-files. Data-files must be generated by the TransReadFinder-pipeline and in json-format.
            <v-file-input
              multiple
              hide-details
              accept=".json"
              @change="loadExperimentFiles"
            />
          </v-card-text>
        </v-card>
      </v-row>
      <template v-if="areExperimentsLoaded">
      <v-row>
       
          <v-tabs align-with-title v-model="tab" style="margin: 10px; width: 100%">
            <v-tab key="project">Projects</v-tab>
            <v-tab key="candidate">Candidates</v-tab>
          </v-tabs>
           <v-tabs-items v-model="tab" style="margin: 10px; width: 100%">
            <v-tab-item key="project"> 
               <v-card dark style="margin: 10px; width: 100%">
            <v-card-title> Analysed {{experiment_information.length}} projects </v-card-title>
            <v-card-text>
              Download
              <v-btn icon @click="downloadExcelTable('projectsTable', 'projects_table')">
                <v-icon>mdi-microsoft-excel</v-icon>
              </v-btn>
              <v-data-table
                dense
                dark
                ref="projectsTable"
                style="margin: 10px; cursor:pointer;"
                :headers="headers"
                :items="experiment_information"
                :items-per-page="-1"
                :sort-by="['score']"
                :sort-desc="[true]"
                class="elevation-1"
                @click:row="onProjectRowClicked"
              >
                <template v-slot:[`item.link`]="{ value }">
                  <a :href="value" target="blank">
                    {{ value }}
                  </a>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
            </v-tab-item>
            <v-tab-item key="candidate">
              <v-card dark style="margin: 10px; width: 100%">
            <v-card-title> Found {{candidate_information.length}} candidates in {{experiment_information.length}} projects</v-card-title>
            <v-card-text>
              Download
              <v-btn icon @click="downloadExcelTable('candidatesAllTable', 'candidates_all')">
                <v-icon>mdi-microsoft-excel</v-icon>
              </v-btn>
              <v-data-table
                dense
                dark
                ref="candidatesAllTable"
                style="margin: 10px"
                :headers="headers2"
                :items="candidate_information"
                :items-per-page="-1"
                :sort-by="['genename']"
                :sort-desc="[false]"
                class="elevation-1"
              >
                <template #item="{item}">
                  <tr :style="customRowStyle(item) + 'cursor:pointer'"  @click="onFragmentRowClicked(item)">
                    <td >{{item.genename}}</td>
                    <td>{{item.protein_description}}</td>
                    <td>{{item.tissue}}</td>
                    <td>{{item.proteinid}}</td>
                    <td class="trf-number-cell">{{item.score}}</td>
                    <td class="trf-number-cell">{{item.sequence_coverage}}</td>
                    <td :style="customRowStyleOfftargets(item) + 'cursor:pointer'">{{item.offtargets}}</td>
                    <td :style="customRowStyleStop(item) + 'cursor:pointer'">{{item.stopcodon}}</td>
                    <td>{{item.downstream}}</td>
                    <td :style="customRowStylePfam(item) + 'cursor:pointer'">{{item.pfam_domain_in_extension}}</td>
                    <td>{{item.localization}}</td>
                    <td :style="customRowStyleSignals(item) + 'cursor:pointer'">{{item.signals}}</td>
                    <!-- <td>{{item.peptide}}</td>
                    <td>{{item.upstream}}</td> -->
                    <td>{{item.projectID}}</td>
                    <td>{{item.counter}}</td>
                  </tr>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
            </v-tab-item>
            </v-tabs-items>
      </v-row>
      </template>
    </v-col>
  </div>
</template>

<script>
import {exportExcelTable} from '../exportUtils';

export default {
  data() {
    return {
      headers: [],
      headers2:[],
      experiment_information: [],
      candidate_information: [],
      tab: null,
    };
  },
  components: {},
  mounted() {
    this.headers = [
      {
        text: "Project",
        value: "projectID",
      },
      {
        text: "Number of Candidates",
        value: "no_of_candidates",
      },
      {
        text: "Title",
        align: "start",
        sortable: true,
        value: "title",
      },
      {
        text: "Organism",
        sortable: true,
        value: "organism",
      },
      {
        text: "Disease",
        sortable: true,
        value: "disease",
      },
      {
        text: "Tissue",
        sortable: true,
        value: "tissue",
      },
      {
        text: "Instrument",
        sortable: true,
        value: "instrument",
      },
      {
        text: "Affiliation",
        value: "university",
      },
      {
        text: "Link",
        value: "link",
      },
      { text: "Number of Experiments", value: "no_of_experiments" },
      {
        text: "LDHB",
        value: "ldhb_found",
      },
      {
        text: "MDH1",
        value: "mdh1_found",
      },
      {
        text: "TGA [%]",
        value: "percentage_of_tga",
      },
    ];
    this.headers2 = [
      {
        text: "Gene name",
        value: "genename",
      },
      {
        text: "Protein description",
        value: "protein_description"
      },
      {
        text: "Tissue",
        value: "tissue",
      },
      {
        text: "Protein ID",
        value: "proteinid",
      },
      {
        text: "Score",
        align: "start",
        sortable: true,
        value: "score",
      },
      {
        text: "Sequence coverage [%]",
        sortable: true,
        value: "sequence_coverage",
      },
      {
        text: "Offtargets",
        sortable: true,
        value: "offtargets",
      },
      {
        text: "Stopcodon",
        value: "stopcodon",
      },
      {
        text: "Downstream",
        value: "downstream",
      },
      {
        text: "Pfam domain in extension",
        sortable: true,
        value: "pfam_domain_in_extension"
      },
      {
        text: "DeepLoc localization",
        sortable: true,
        value: "localization"
      },
      {
        text: "DeepLoc signals",
        sortable: true,
        value: "signals"
      },
      // {
      //   text: "Fragment",
      //   value: "peptide",
      // },
      // {
      //   text: "Upstream",
      //   value: "upstream",
      // },
       {
        text: "Project",
        value: "projectID",
      },
      {
        text: "Project occurences",
        value: "counter",
      },
     
    ];
     this.updateExperimentInfo();
  },
  computed: {
    areExperimentsLoaded() {
      return this.experiment_information.length > 0;
    }
  },
  methods: {
    updateExperimentInfo() {
      let project_information = this.$store.state.data;
      this.experiment_information = [];
      this.candidate_information = [];
      for (const projectID in project_information) {
        const project = project_information[projectID];
        const fragment_information = project.fragment_information;
        const no_of_candidates = Object.keys(fragment_information).length;
        let tissue = project.tissue
          .replace("[", "")
          .replace("]", "")
          .replaceAll("'", "");
        if(tissue == ""){
          tissue = "-";
        }
        
        this.experiment_information.push({
          projectID: projectID,
          title: project.title,
          organism: project.organism,
          disease: project.disease,
          tissue: tissue,
          instrument: project.instrument,
          university: project.university,
          link: project.link,
          no_of_candidates: no_of_candidates,
          ldhb_found: project.ldhb_found,
          mdh1_found:  project.mdh1_found,
          percentage_of_tga: project.percentage_of_tga,
          no_of_experiments: project.no_of_experiments,
        });
        
        for(const fragment of fragment_information) {
          const genename = fragment.genename;
          let offtargets_dict = fragment.offtargets;
          let offtargets = "";
          if (offtargets_dict != "None") {
            let offtargets_list = offtargets_dict.split("}},");
            for (let target of offtargets_list) {
              target = target.replaceAll("{", "").replaceAll("}", "");
              const proteinID = target.split(":")[0];
              if(!offtargets.includes(proteinID)){
                offtargets = offtargets + proteinID + ",";
              }
            }
          }else{
            offtargets = "-";
          }
          const deeploc_info = fragment.deeploc_info;
          let localization = "";
          let signals = "";
          for(let info of deeploc_info){
            if(info[0]['localization']){
              localization =  info[0]['localization'];
            }
            else{
              localization = "-";
            }
            if(info[0]['signals']){
              signals = info[0]['signals'];
            }
            else{
              signals = "-";
            }
          }
          let downstream = fragment.downstream;
          downstream = downstream.slice(0,5);
          let pfam_domain_in_extension = fragment.pfam_domain_in_extension;
          if (pfam_domain_in_extension == ""){
            pfam_domain_in_extension = "-";
          }
          this.candidate_information.push({
            id: fragment.id,
            counter: 1,
            projectID: projectID,
            genename: genename,
            protein_description: fragment.protein_description,
            tissue: tissue,
            proteinid: fragment.proteinid,
            score: (fragment.score).toFixed(1),
            sequence_coverage: (fragment.sequence_coverage).toFixed(1),
            offtargets: offtargets,
            peptide: fragment.peptide,
            upstream: fragment.upstream,
            stopcodon: fragment.stopcodon,
            downstream: downstream,
            pfam_domain_in_extension: pfam_domain_in_extension,
            localization: localization,
            signals: signals,
          });
        }
      }
      for (let i = 0; i < this.candidate_information.length; i++) {
        const info = this.candidate_information[i];
        if(info.genename != ""){
          info.counter = this.candidate_information.filter(
            x => x.genename === info.genename
          ).length;
        }
      }
    },
    loadExperimentFiles(e) {
      const files = [...e];
      for (let i = 0; i < files.length; i++) {
        const reader = new FileReader();
        reader.onload = (result) => {
          const data = JSON.parse(result.target.result);
          this.$store
            .dispatch("processProject", data)
            .finally(() => this.updateExperimentInfo());
        };
        reader.readAsText(files[i]);
      }
    },
    onProjectRowClicked(item) {
      this.$router.push("/experiment/" + item.projectID);
    },
    onFragmentRowClicked(item) {
        this.$router.push(
        "/fragment/" + item.projectID + "/" + item.id
      );
    },
    customRowStyle(item) {
      if (item.counter > 1) {
        return "background-color: #FFCCFF; color: #000000;";
      }
      return '';
    },
    customRowStyleStop(item) {
      if (item.stopcodon == "TGA") {
        return "color: #32CD32;";
      }
      return '';
    },
    customRowStyleSignals(item) {
      if (item.signals == "Peroxisomal targeting signal") {
        return "color: #BA55D3;";
      }
      return '';
    },
    customRowStyleOfftargets(item) {
      if (item.offtargets != "-") {
        return "color: #FF0000;";
      }
      return '';
    },
    customRowStylePfam(item) {
      if (item.pfam_domain_in_extension == "in extension" || item.pfam_domain_in_extension == "overlap") {
        return "background-color: #FF7F24; color: #000000";
      }
      return '';
    },
    downloadExcelTable(refName, filename) {
      exportExcelTable(this.$refs[refName].$el, filename);
    }
  },
};
</script>