<template>
  <v-img
    gradient="to top right, rgba(100,115,201,.7), rgba(25,32,72,.7)"
    :lazy-src="currentImage"
    height="100%"
    contain
  ></v-img>
</template>

<script>
export default {
  data() {
    return {
        images: [
            require('@/assets/TL_1.png'),
            require('@/assets/TL_2.png'),
            require('@/assets/TL_3.png'),
            require('@/assets/TL_4.png'),
            require('@/assets/TL_5.png'),
            require('@/assets/TL_6.png'),
            require('@/assets/TL_7.png'),
            require('@/assets/TL_8.png'),
            require('@/assets/TL_9.png'),
            require('@/assets/TL_10.png'),
            require('@/assets/TL_11.png'),
            require('@/assets/TL_12.png'),
            require('@/assets/TL_13.png'),
            require('@/assets/TL_14.png'),
            require('@/assets/TL_15.png'),
            require('@/assets/TL_16.png'),
            require('@/assets/TL_17.png'),
            require('@/assets/TL_18.png'),
            
        ],
        currentImage: null,
        currentIndex: 0,
    };
  },
  mounted() {
    this.currentIndex = 0;
    this.currentImage = this.images[this.currentIndex];
    //console.log(this.currentImage);
    setTimeout(this.moveToNextImage, 2000);
  },
  methods: {
    moveToNextImage() {
        this.currentIndex = (this.currentIndex + 1) % this.images.length;
        this.currentImage = this.images[this.currentIndex];
        setTimeout(this.moveToNextImage, 2000);
    }
  }
};
</script>
