<template>
  <v-card dark class="fill-height">
    <v-card-title>Alignment</v-card-title>
    <v-card-text>
      <!--<pre>
<template v-for="item in alignment">{{ item + '\n' }}</template>
        </pre>-->
      <pre
        ref="container"
        v-text="alignmentLines"
        style="overflow-x: scroll"
      ></pre>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data() {
    return {
      alignmentLines: "",
    };
  },
  props: {
    sequenceA: {
      required: true,
      type: String,
    },
    alignment: {
      required: true,
      type: String,
    },
  },
  mounted() {
    let alignment = this.alignment;
    let alignment_lines = alignment.split("\n");
    let alignments = [];
    for (let line of alignment_lines) {
      line = line.replace("\r", "");
      line = line.replace('"', "");
      alignments.push(line);
    }
    alignments = alignments.slice(0, -1);
    let sequence_A = this.sequenceA;
    let alignment_line1 = alignments[0];
    let alignment_line2 = alignments[1];
    let alignment_line3 = alignments[2];
    let align = [];
    for (var i = 0; i < sequence_A.length + 110; i += 110) {
      align.push(
        sequence_A.slice(i, i + 110),
        alignment_line1.slice(i, i + 110),
        alignment_line2.slice(i, i + 110),
        alignment_line3.slice(i, i + 110)
      );
    }
    this.alignmentLines = align;
    this.alignmentLines = this.sequenceA + "\n" + this.alignment;
    this.$nextTick(() => {
      this.$refs.container.scrollTo(this.$refs.container.scrollWidth, 0);
    });
  },
};
</script>