<template>
  <div>
    <v-card dark>
      <v-card-title>
        <!-- <div>Pfam Results</div> -->
        <v-list-item three-line>
      <v-list-item-content>
        <div class="text-overline mb-4">
           <v-app-bar
            flat
            color="rgba(0, 0, 0, 0)"
          >
            <v-toolbar-title >
              
            </v-toolbar-title>
            <v-spacer></v-spacer>

            <v-btn
              color="white"
              icon @click = goToAlignment
            >
              <v-icon>mdi-arrow-up-bold-box-outline</v-icon>
            </v-btn>
          </v-app-bar>
        </div>
        <v-list-item-title class="text-h5 mb-1">
          Pfam results
        </v-list-item-title>
      </v-list-item-content>

      <v-list-item-avatar
        tile
        size="50"
        color="#FF7F24"
      ></v-list-item-avatar>
    </v-list-item>
      </v-card-title>
      <v-card-text>
        Pfam is a protein familiy database that classifies protein sequences into families, domains and clans. Here, Pfam predicts regions that contain domains in the found protein.
      <a href="https://pfam-legacy.xfam.org/" target="_blank">
        Go to Pfam <v-icon>mdi-open-in-new</v-icon>
      </a>
        <div>
          <v-data-table
            dense
            dark
            style="margin: 5px"
            :headers="headers"
            :items="pfam_info"
            :items-per-page="10"
            class="elevation-1"
          >
         
            <template #item="{item}">
                  <tr :style="customRowStyle(item) + 'cursor:pointer'" @click="onFragmentRowClicked(item)">
                    <td>{{item.proteinID}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.accession}}</td>
                    <td class="trf-number-cell">{{item.evalue}}</td>
                    <td>{{item.description}}</td>
                    <td>{{item.type}}</td>
                    <td>{{item.clan}}</td>
                    <td class="trf-number-cell">{{item.startposition}}</td>
                    <td class="trf-number-cell">{{item.endposition}}</td>
                    <td><a :href="item.link" target="blank">
                {{ item.link }}
              </a></td>
                  </tr>
                </template>
          </v-data-table>
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      headers: [],
      pfam_info: [],
    };
  },
  props: {
    proteinId: {
      required: true,
      type: String,
    },
  },
  methods: {
    customRowStyle(item) {
      if (item.domain_in_extension == true) {
        return "background-color: #FFCCFF; color: #000000;";
      }
      return "";
    },
  },
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    let pfam_info_json = entry["pfam_info"];
    let pfam_info = [];
    let sequence_length_with_ext = entry.sequence_lengthaa;
    let extension = entry.extension_sequence;
    const sequence_length = sequence_length_with_ext - extension.length;
    let found_pfam_hits = [];
    for (let result_list of pfam_info_json) {
      for (let result of result_list) {
        if (result.length != 0) {
          let accession = result[2];
          // https://identifiers.org/pfam:PF13499
          // https://www.ebi.ac.uk/interpro/entry/pfam/PF13499/
          let link = "https://pfam.xfam.org/family/" + accession;
          const startposition = result[7];
          const endposition = result[8];
          if (!(found_pfam_hits.includes((startposition,endposition)))){
            found_pfam_hits.push((startposition,endposition));
            let domain_in_extension = false;
            if (
              sequence_length < startposition &&
              sequence_length < endposition
            ) {
              domain_in_extension = true;
            }
            pfam_info.push({
              proteinID: result[0],
              name: result[1],
              accession: accession,
              evalue: result[3],
              description: result[4],
              type: result[5],
              clan: result[6],
              startposition: startposition,
              endposition: endposition,
              //hmm: result[9],
              //match: result[10],
              //probability: result[11],
              link: link,
              domain_in_extension: domain_in_extension,
            });
          }
        }
      }
    }
    this.pfam_info = pfam_info;
    //console.log("pfam",pfam_info);

    this.headers = [
      {
        text: "ID",
        align: "start",
        sortable: false,
        value: "proteinID",
      },
      { text: "name", value: "name" },
      { text: "accession", value: "accession" },
      { text: "evalue", value: "evalue" },
      { text: "description", value: "description" },
      { text: "type", value: "type" },
      { text: "clan", value: "clan" },
      { text: "startposition", value: "startposition" },
      { text: "endposition", value: "endposition" },
      /* { text: "hmm", value: "hmm" },
      { text: "match", value: "match" },
      { text: "probability", value: "probability" }, */
      { text: "link", value: "link" },
    ];
  },
};
</script>