<template>
  <div>
    <v-row dark>
      <v-col cols="12" sm="3" md="3">
          <v-card dark style="margin= 5px; height: 100%; width:100%">
            <v-card-title>General peptide information and scores</v-card-title>
            <v-card-text>
              <v-simple-table dense dark >
                <thead>
                  <tr>
                    <th class="text-left">Peptide:</th>
                    <th class="text-left">{{ peptide }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Length:</th>
                    <th class="text-left">{{ length }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <span v-bind="attrs" v-on="on">Score:</span>
                        </template>
                        <span
                          >The Andromeda score obtains information on how well an
                          acquired spectrum matches the theoretical fragment
                          masses.
                        </span>
                      </v-tooltip>
                    </th>
                    <th class="text-left">{{ score }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <span v-bind="attrs" v-on="on">Delta score:</span>
                        </template>
                        <span
                          >Delta score is the score difference to the second best
                          identified peptide.</span
                        >
                      </v-tooltip>
                    </th>
                    <th class="text-left">{{ delta_score }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Missed cleavages:</th>
                    <th class="text-left">{{ missed_cleavages }}</th>
                  </tr>
                </thead>
              </v-simple-table>
            </v-card-text>
          </v-card>
       
      </v-col>
      <v-col cols="12" sm="3" md="3">
        
          <v-card dark style="margin= 5px; height: 100%; width: 100%">
            <v-card-title>Mass information</v-card-title>
            <v-card-text>
              <v-simple-table dense dark>
                <thead>
                  <tr>
                    <th class="text-left">Mass:</th>
                    <th class="text-left">{{ mass }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">m/z:</th>
                    <th class="text-left">{{ m_z }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <span v-bind="attrs" v-on="on">MS/MS m/z:</span>
                        </template>
                        <span
                          >The m/z used for fragmentation (not necessarily the
                          monoisotopic m/z).</span
                        >
                      </v-tooltip>
                    </th>
                    <th class="text-left">{{ msms_m_z }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Charge:</th>
                    <th class="text-left">{{ charge }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <span v-bind="attrs" v-on="on">PEP:</span>
                        </template>
                        <span
                          >Posterior Error Probability of the identification. This
                          value essentially operates as a p-value, where smaller
                          is more significant.</span
                        >
                      </v-tooltip>
                    </th>
                    <th class="text-left">{{ pep }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Mass error [ppm]:</th>
                    <th class="text-left">{{ mass_error_ppm }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Uncalibrated mass error [ppm]:</th>
                    <th class="text-left">{{ uncal_mass_error_ppm }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Mass error [Da]:</th>
                    <th class="text-left">{{ mass_error_da }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Uncalibrated mass error [Da]:</th>
                    <th class="text-left">{{ uncal_mass_error_da }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">MS/MS Scan number:</th>
                    <th class="text-left">{{ msms_scan_no }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <span v-bind="attrs" v-on="on">MS/MS count:</span>
                        </template>
                        <span
                          >The number of sequencing events for this sequence,
                          which matches the number of identifiers stored in the
                          column MS/MS IDs.</span
                        >
                      </v-tooltip>
                    </th>
                    <th class="text-left">{{ msms_count }}</th>
                  </tr>
                </thead>
              </v-simple-table>
            </v-card-text>
          </v-card>
        
      </v-col>
      <v-col cols="12" sm="6" md="6">
        <v-card dark style="height = 100%; margin= 5px" >
          <v-card-title>
            <div>Peptides: Aminoacid counts</div>
          </v-card-title>
          <v-card-text>
            <div
              ref="pie"
              style="padding: 2%; width: 100%; height: 400px; position: relative"
            >Composition of amino acids</div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
      <v-row dark>
        <v-col cols="12" sm="6" md="6">
          <v-card dark style="margin= 5px; height: 100%; width: 100%">
            <v-card-title>Chromatographic details</v-card-title>
            <v-card-text>
              <v-simple-table dense dark >
                <thead>
                  <tr>
                    <th class="text-left">Intensity:</th>
                    <th class="text-left">{{ intensity }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Retention time:</th>
                    <th class="text-left">{{ retention_time }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Retention length:</th>
                    <th class="text-left">{{ retention_length }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Number of data points:</th>
                    <th class="text-left">{{ no_of_data_points }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Number of scans:</th>
                    <th class="text-left">{{ no_of_scans }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Number of isotopic peaks:</th>
                    <th class="text-left">{{ no_of_isotopic_peaks }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <span v-bind="attrs" v-on="on">PIF:</span>
                        </template>
                        <span
                          >Short for Parent Ion Fraction; indicates the fraction
                          the target peak makes up of the total intensity in the
                          inclusion window.</span
                        >
                      </v-tooltip>
                    </th>
                    <th class="text-left">{{ pif }}</th>
                  </tr>
                </thead>
              </v-simple-table>
            </v-card-text>
          </v-card>
        </v-col>
      <v-col cols="12" sm="6" md="6">
          <v-card dark style="margin= 5px; height: 100%; width: 100%">
            <v-card-title>Modifications</v-card-title>
            <v-card-text>
              <v-simple-table dense dark style="margin: 5px">
                <thead>
                  <tr>
                    <th class="text-left">Modifications found in this peptide:</th>
                    <th class="text-left">{{ modifications }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Modified sequence:</th>
                    <th class="text-left">{{ modified_sequence }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Oxidation (M) probabilities:</th>
                    <th class="text-left">{{ oxidation_m_probabilities }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Oxidation (M) score difference:</th>
                    <th class="text-left">{{ oxidation_m_score_diffs }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Oxidation (M):</th>
                    <th class="text-left">{{ oxidation_m }}</th>
                  </tr>
                  <tr>
                    <th class="text-left">Acetyl (Protein N-term):</th>
                    <th class="text-left">{{ acetyl_protein_n_term }}</th>
                  </tr>
                    </thead>
              </v-simple-table>
            </v-card-text>
          </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  data() {
    return {
      peptide: String,
      length: String,
      missed_cleavages: String,
      delta_score: String,
      score: String,
      intensity: String,
      pep: String,
      mass_error_ppm: String,
      mass_error_da: String,
      uncal_mass_error_ppm: String,
      uncal_mass_error_da: String,
      msms_scan_no: String,
      modifications: String,
      modified_sequence: String,
      oxidation_m_probabilities: String,
      oxidation_m_score_diffs: String,
      oxidation_m: String,
      acetyl_protein_n_term: String,
      msms_m_z: String,
      charge: String,
      m_z: String,
      mass: String,
      msms_count: String,
      retention_time: String,
      retention_length: String,
      no_of_data_points: String,
      no_of_scans: String,
      no_of_isotopic_peaks: String,
      pif: String,
    };
  },
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    const peptide_info = entry["peptide_info"];
    //general info and scores
    this.missed_cleavages = peptide_info.split("missed_cleavages':")[1].split(",")[0].replaceAll("'", "");
    this.score = peptide_info.split("score': ")[1].split(",")[0].replaceAll("'", "");
    this.intensity = peptide_info.split("intensity': ")[1].split(",")[0].replaceAll("'", "");
    const evidence_info = entry["evidence_info"];
    this.peptide = evidence_info.split("sequence': ")[1].split(",")[0].replaceAll("'", "");
    this.length = evidence_info.split("length': ")[1].split(",")[0].replaceAll("'", "");
    this.delta_score = evidence_info.split("delta_score': ")[1].split(",")[0].replaceAll("'", "");
    this.pep = evidence_info.split("pep': ")[1].split(",")[0].replaceAll("'", "");
    //mass information
    this.mass_error_ppm = entry["mass_errorppm"];
    this.mass_error_da = entry["mass_errorda"];
    this.uncal_mass_error_ppm = entry["uncalibrated_mass_errorppm"];
    this.uncal_mass_error_da = entry["uncalibrated_mass_errorda"];
    this.msms_scan_no = entry["msms_scan_number"];
    this.msms_m_z = evidence_info.split("msms_m_z': ")[1].split(",")[0].replaceAll("'", "");
    this.charge = evidence_info.split("charge': ")[1].split(",")[0].replaceAll("'", "");
    this.m_z = evidence_info.split("m_z': ")[1].split(",")[0].replaceAll("'", "");
    this.mass = evidence_info.split("mass': ")[1].split(",")[0].replaceAll("'", "");
    this.msms_count = evidence_info.split("msms_count': ")[1].split(",")[0].replaceAll("'", "");
    //chromatographic details
    this.retention_time = evidence_info.split("retention_time': ")[1].split(",")[0].replaceAll("'", "");
    this.retention_length = evidence_info.split("retention_length': ")[1].split(",")[0].replaceAll("'", "");
    this.no_of_data_points = evidence_info.split("no_of_data_points': ")[1].split(",")[0].replaceAll("'", "");
    this.no_of_scans = evidence_info.split("no_of_scans': ")[1].split(",")[0].replaceAll("'", "");
    this.no_of_isotopic_peaks = evidence_info.split("no_of_isotopic_peaks': ")[1].split(",")[0].replaceAll("'", "");
    this.pif = evidence_info.split("pif': ")[1].split(",")[0].replaceAll("'", "");
    //modifications
    this.modifications = evidence_info.split("modifications': ")[1].split(",")[0].replaceAll("'", "");
    this.modified_sequence = evidence_info.split("modified_sequence': ")[1].split(",")[0].replaceAll("'", "");
    this.oxidation_m_probabilities = evidence_info.split("oxidation_m_probabilities': ")[1].split(",")[0].replaceAll("'", "");
    this.oxidation_m_score_diffs = evidence_info.split("oxidation_m_score_diffs': ")[1].split(",")[0].replaceAll("'", "");
    this.acetyl_protein_n_term = evidence_info.split("acetyl_protein_n_term': ")[1].split(",")[0].replaceAll("'", "");
    this.oxidation_m = evidence_info.split("oxidation_m': ")[1].split(",")[0].replaceAll("'", "");
    //aminoacid counts
    const a_count = parseInt(peptide_info.split("a_count': ")[1].split(",")[0].replaceAll("'", ""));
    const r_count = parseInt(peptide_info.split("r_count': ")[1].split(",")[0].replaceAll("'", ""));
    const n_count = parseInt(peptide_info.split("n_count': ")[1].split(",")[0].replaceAll("'", ""));
    const d_count = parseInt(peptide_info.split("d_count': ")[1].split(",")[0].replaceAll("'", ""));
    const c_count = parseInt(peptide_info.split("c_count': ")[1].split(",")[0].replaceAll("'", ""));
    const q_count = parseInt(peptide_info.split("q_count': ")[1].split(",")[0].replaceAll("'", ""));
    const e_count = parseInt(peptide_info.split("e_count': ")[1].split(",")[0].replaceAll("'", ""));
    const g_count = parseInt(peptide_info.split("g_count': ")[1].split(",")[0].replaceAll("'", ""));
    const h_count = parseInt(peptide_info.split("h_count': ")[1].split(",")[0].replaceAll("'", ""));
    const i_count = parseInt(peptide_info.split("i_count': ")[1].split(",")[0].replaceAll("'", ""));
    const l_count = parseInt(peptide_info.split("l_count': ")[1].split(",")[0].replaceAll("'", ""));
    const k_count = parseInt(peptide_info.split("k_count': ")[1].split(",")[0].replaceAll("'", ""));
    const m_count = parseInt(peptide_info.split("m_count': ")[1].split(",")[0].replaceAll("'", ""));
    const f_count = parseInt(peptide_info.split("f_count': ")[1].split(",")[0].replaceAll("'", ""));
    const p_count = parseInt(peptide_info.split("p_count': ")[1].split(",")[0].replaceAll("'", ""));
    const s_count = parseInt(peptide_info.split("s_count': ")[1].split(",")[0].replaceAll("'", ""));
    const t_count = parseInt(peptide_info.split("t_count': ")[1].split(",")[0].replaceAll("'", ""));
    const w_count = parseInt(peptide_info.split("w_count': ")[1].split(",")[0].replaceAll("'", ""));
    const y_count = parseInt(peptide_info.split("y_count': ")[1].split(",")[0].replaceAll("'", ""));
    const v_count = parseInt(peptide_info.split("v_count': ")[1].split(",")[0].replaceAll("'", ""));
    const u_count = parseInt(peptide_info.split("u_count': ")[1].split(",")[0].replaceAll("'", ""));
    const o_count = parseInt(peptide_info.split("o_count': ")[1].split(",")[0].replaceAll("'", ""));

    const amino_acid_counts = [
      { value: a_count, name: "Alanine (A)" }, { value: r_count, name: "Arginine (R)"}, { value: n_count, name: "Asparagine (N)" },
      { value: d_count, name: "Aspartic acid (D)" }, { value: c_count, name: "Cysteine (C)" }, { value: q_count, name: "Glutamine (Q)" },
      { value: e_count, name: "Glutamic acid (E)" }, { value: g_count, name: "Glycine (G)" }, { value: h_count, name: "Histidine (H)" },
      { value: i_count, name: "Isoleucine (I)" }, { value: l_count, name: "Leucine (L)" }, { value: k_count, name: "Lysine (K)" },
      { value: m_count, name: "Methionine (M)" }, { value: f_count, name: "Phenylalanine (F)" }, { value: p_count, name: "Proline (P)" },
      { value: s_count, name: "Serine (S)" }, { value: t_count, name: "Threonine (T)" }, { value: w_count, name: "Tryptophan (W)" },
      { value: y_count, name: "Tyrosine (Y)" }, { value: v_count, name: "Valine (V)" }, { value: u_count, name: "Selenocysteine (U)" },
      { value: o_count, name: "Pyrrolysine (O)" },
    ];

    this.echartBarplot = echarts.init(this.$refs["pie"], "dark");
    const option = {
      tooltip: {
        trigger: "item",
      },
      legend: {
        type: "scroll",
        orient: "vertical",
        right: 10,
        top: 20,
        bottom: 20,
      },
      series: [
        {
          name: "Amino acid counts of",
          type: "pie",
          radius: ["40%", "70%"],
          avoidLabelOverlap: false,
          itemStyle: {
            borderRadius: 10,
            borderColor: "#fff",
            borderWidth: 2,
          },
          label: {
            show: false,
            position: "center",
          },
          emphasis: {
            label: {
              show: true,
              fontSize: "20",
              fontWeight: "bold",
            },
          },
          labelLine: {
            show: false,
          },
          data: amino_acid_counts,
        },
      ],
    };
    this.echartBarplot.setOption(option);
  },
};
</script>
