<template>
  <div>
    <v-card dark>
      <v-card-title>
        <div>PieChart Peptides</div>
      </v-card-title>
      <v-card-text>
        Comparison between
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <span v-bind="attrs" v-on="on">peptides</span>
          </template>
          <span
            >The total number of peptide sequences associated with the protein
            group (i.e. for all the proteins in the group).</span
          > </v-tooltip
        >,
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <span v-bind="attrs" v-on="on">unique peptides</span>
          </template>
          <span>
            The total number of unique peptides associated with the protein
            group.</span
          >
        </v-tooltip>
        and
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <span v-bind="attrs" v-on="on">razor + unique peptides</span>
          </template>
          <span
            >The total number of razor + unique peptides associated with the
            protein group (i.e. these peptides are shared with another protein
            group).</span
          >
        </v-tooltip>
        found in this proteingroup.
        <div
          ref="pie"
          style="padding: 2%; width: 100%; height: 600px; position: relative"
        ></div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    let experiment = [];
    let data = [];
    let intensities = entry["intensities"];
    intensities = intensities
      .replace("{", "")
      .replace("}", "")
      .replaceAll("'", "");
    intensities = intensities.replace(/ /g, "");
    intensities = intensities.split(",");
    let proteinGroup_info = entry["proteinGroup_info"];
    let peptides_info = proteinGroup_info
      .split("peptides_info': [")[1]
      .split("]")[0]
      .replaceAll("'", "")
      .split(",");
    let unique_peptides_info = proteinGroup_info
      .split("unique_peptides_info': [")[1]
      .split("]")[0]
      .replaceAll("'", "")
      .split(",");
    let razor_unique_peptides_info = proteinGroup_info
      .split("razor_unique_peptides_info': [")[1]
      .split("]")[0]
      .replaceAll("'", "")
      .split(",");
    //console.log(proteinGroup_info);;
    for (let item of intensities) {
      let x = item.split(":");
      experiment.push(x[0]);
    }

    for (let i = 0; i < experiment.length; i++) {
      data.push([
        experiment[i],
        parseInt(peptides_info[i].replace(" ", "")),
        0,
      ]);
    }
    for (let i = 0; i < experiment.length; i++) {
      data.push([
        experiment[i],
        parseInt(unique_peptides_info[i].replace(" ", "")),
        1,
      ]);
    }
    for (let i = 0; i < experiment.length; i++) {
      data.push([
        experiment[i],
        parseInt(razor_unique_peptides_info[i].replace(" ", "")),
        2,
      ]);
    }

    this.echartBarplot = echarts.init(this.$refs["pie"], "dark");

    // // let equal_names = [];
    // let counter = 1;
    // for (let name in experiment) {
    //   counter +=1;
    //   console.log(name);
    // //   // name = name.slice(0, name.length - 1);
    // //   // if (!equal_names.includes(name)) {
    // //   //   equal_names.push(name);
    // //   // }
    // //   equal_names.push(counter);
    // }
    // // const maxCommonPrefixLength = Math.min(...equal_names.map((o) => o.length));

    const option = {
      title: [
        {
          text: "Peptides",
          left: "center",
          top: "5%",
        },
        {
          subtext: "Peptides",
          left: "25%",
          top: "80%",
          textAlign: "center",
        },
        {
          subtext: "Unique peptides",
          left: "50%",
          top: "80%",
          textAlign: "center",
        },
        {
          subtext: "Razor unique peptides",
          left: "75%",
          top: "80%",
          textAlign: "center",
        },
      ],
      tooltip: {
        trigger: "item",
        axisPointer: { type: "shadow" },
      },
      legend: {
        type: "scroll",
        orient: "vertical",
        right: 10,
        top: 20,
        bottom: 20,
      },
      dataset: [
        {
          source: [["Sample", "Count", "kind"]].concat(data),
        },
        {
          transform: {
            type: "filter",
            config: { dimension: "kind", value: 0 },
          },
        },
        {
          transform: {
            type: "filter",
            config: { dimension: "kind", value: 1 },
          },
        },
        {
          transform: {
            type: "filter",
            config: { dimension: "kind", value: 2 },
          },
        },
      ],
      series: [
        {
          type: "pie",
          radius: 100,
          center: ["50%", "25%"],
          emphasis: { focus: "self" },
          label: {show: false,},
          // label: {
          //   counter,
          //   //formatter: (x) => x.data[0].substring(maxCommonPrefixLength),
          // },
          datasetIndex: 1,
        },
        {
          type: "pie",
          radius: 100,
          center: ["50%", "50%"],
          emphasis: { focus: "self" },
          label: {show: false,},
          //   counter,
          //   //formatter: (x) => x.data[0].substring(maxCommonPrefixLength),
          // 
          datasetIndex: 2,
        },
        {
          type: "pie",
          radius: 100,
          center: ["50%", "75%"],
          emphasis: { focus: "self" },
          label: {show: false,},
          // label: {
          //   counter,
          //   // formatter: (x) => x.data[0].substring(maxCommonPrefixLength),
          // },
          datasetIndex: 3,
        },
      ],
      // Optional. Only for responsive layout:
      media: [
        {
          query: { minAspectRatio: 1 },
          option: {
            series: [
              { center: ["25%", "50%"] },
              { center: ["50%", "50%"] },
              { center: ["75%", "50%"] },
            ],
          },
        },
        {
          option: {
            series: [
              { center: ["50%", "25%"] },
              { center: ["50%", "50%"] },
              { center: ["50%", "75%"] },
            ],
          },
        },
      ],
    };
    this.echartBarplot.setOption(option);
  },
};
</script>
