<template>
  <v-card dark style="margin: 5px">
    <v-card-title>
      <!-- <div>1. Phobius Results</div> -->
      <v-list-item three-line>
      <v-list-item-content>
        <div class="text-overline mb-4">
           <v-app-bar
            flat
            color="rgba(0, 0, 0, 0)"
          >
            <v-toolbar-title >
              
            </v-toolbar-title>
            <v-spacer></v-spacer>

            <v-btn
              color="white"
              icon @click = goToAlignment
            >
              <v-icon>mdi-arrow-up-bold-box-outline</v-icon>
            </v-btn>
          </v-app-bar>
        </div>
        <v-list-item-title class="text-h5 mb-1">
          Phobius results
        </v-list-item-title>
      </v-list-item-content>

      <v-list-item-avatar
        tile
        size="50"
        color="#008B8B"
      ></v-list-item-avatar>
    </v-list-item>
    </v-card-title>
    <v-card-text>
      Phobius predicts transmembrane topologies and signal peptides. Here, the horizonal arrow marks the start of the extension sequence.
      <a href="https://phobius.sbc.su.se/" target="_blank">
        Go to Phobius <v-icon>mdi-open-in-new</v-icon>
      </a>
      <div
        ref="image"
        style="padding: 2%; width: 100%; height: 600px; position: relative"
      ></div>
    </v-card-text>
  </v-card>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      headers: [],
      mqinfo: [],
    };
  },
  components: {},
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    const phobius_info = entry.phobius_info;
    const no_aa = [];
    const cytoplasmic = [];
    const non_cytoplasmic = [];
    const transmembrane = [];
    const signalpeptide = [];
    for (let x of phobius_info) {
      no_aa.push(x[0]);
      cytoplasmic.push(100*x[2]);
      non_cytoplasmic.push(100*x[3]);
      transmembrane.push(100*x[4]);
      signalpeptide.push(100*x[5]);
    }
    let sequence_length_with_ext = entry.sequence_lengthaa;
    let extension = entry.extension_sequence;
    const sequence_length = sequence_length_with_ext - extension.length;

    this.echartPlot = echarts.init(this.$refs["image"], "dark");

    const option = {
      color: ["#FF0087", "#80FFA5", "#FFBF00", "#00DDFF"],
      title: {
        text: "Phobius posterior probabilities",
      },
      tooltip: {
        trigger: "axis",
        axisPointer: {
          type: "cross",
          label: {
            backgroundColor: "#6a7985",
          },
        },
      },
      legend: {
        data: [
          "Signalpeptide",
          "Transmembrane",
          "Cytoplasmic",
          "Non cytoplasmic",
        ],
      },
      toolbox: {
        feature: {
          saveAsImage: {},
        },
      },
      grid: {
        left: "3%",
        right: "1%",
        bottom: "3%",
        containLabel: true,
      },
      xAxis: [
        {
          type: "category",
          boundaryGap: false,
          data: no_aa,
        },
      ],
      yAxis: [
        {
          name: "[%]",
          type: "value",
          interval: 25,
        },
      ],
      series: [
        {
          name: "Signalpeptide",
          type: "line",
          markLine: {
            data: [
              {
                0: {
                  symbolSize: 10,
                },
                name: "Horizontal line with Y value at 100",
                xAxis: sequence_length,
              },
            ],
            lineStyle: {cap: "round",},
            label: {
              position: "insideStartTop",
            },
            silent: true,
          },
          stack: "Total",
          smooth: true,
          lineStyle: {
            width: 1,
          },
          showSymbol: false,
          areaStyle: {
            opacity: 0.6,
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "rgb(255, 0, 135)",
              },
              {
                offset: 1,
                color: "rgb(135, 0, 157)",
              },
            ]),
          },
          emphasis: {
            focus: "series",
          },
          data: signalpeptide,
        },
        {
          name: "Transmembrane",
          type: "line",
          stack: "Total",
          smooth: true,
          lineStyle: {
            width: 1,
          },
          showSymbol: false,
          areaStyle: {
            opacity: 0.6,
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "rgb(55, 162, 255)",
              },
              {
                offset: 1,
                color: "rgb(116, 21, 219)",
              },
            ]),
          },
          emphasis: {
            focus: "series",
          },
          data: transmembrane,
        },
        {
          name: "Cytoplasmic",
          type: "line",
          stack: "Total",
          smooth: true,
          lineStyle: {
            width: 1,
          },
          showSymbol: false,
          areaStyle: {
            opacity: 0.6,
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "rgb(128, 255, 165)",
              },
              {
                offset: 1,
                color: "rgb(1, 191, 236)",
              },
            ]),
          },
          emphasis: {
            focus: "series",
          },
          data: cytoplasmic,
        },
        {
          name: "Non cytoplasmic",
          type: "line",
          stack: "Total",
          smooth: true,
          lineStyle: {
            width: 1,
          },
          showSymbol: false,
          areaStyle: {
            opacity: 0.6,
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "rgb(0, 221, 255)",
              },
              {
                offset: 1,
                color: "rgb(77, 119, 255)",
              },
            ]),
          },
          emphasis: {
            focus: "series",
          },
          data: non_cytoplasmic,
        },
      ],
    };
    this.echartPlot.setOption(option);
  },
};
</script>

* {
  margin: 0;
  padding: 0;
}
#chart-container {
  position: relative;
  height: 100%;
  overflow: hidden;
}
