import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Experiment from '../views/Experiment.vue'
import FragmentDetails from '../views/FragmentDetails.vue';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/experiment/:projectID',
    name: 'Experiment',
    component: Experiment
  },
  {
    path: '/fragment/:projectID/:id',
    name: 'Fragment Details',
    component: FragmentDetails
  },
]

const router = new VueRouter({
  routes,
})

router.afterEach(() => {
  window.scrollTo(0, 0);
});

export default router
